.game-item {
    width: 320px;
    height: 300px;
    background-color: #363741;
    border-radius: 15px;
    cursor: pointer;
    position: relative;
}
.game-item:not(:last-child) {
    margin-bottom: 30px;
    margin-right: 50px;
}
.game-item__title {
    font-size: 18px;
    font-weight: bold;
}
.game-item__genre {
    display: flex;
    flex-wrap: wrap;
    margin-top: 10px;
}
.game-item__details {
    padding: 10px 20px;
}
.game-item__description {
    margin-top: 10px;
    font-size: 14px;
}
.game-item__buy{
    position: absolute;
    right: 0;
    bottom: 0px;
}
